@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css);
.site-card-border-less-wrapper {
    padding: 30px;
    background: #ececec;
  }
  

.site-calendar-demo-card {
    width: 300px;
    border: 1px solid #f0f0f0;
    border-radius: 2px;
    background-color: cadetblue;
  }

.owl {
  margin: auto;
  width: 211px;
  height: 108px;
  background-image: url("https://dash.readme.io/img/owl-login.png");
  position: relative;
}
.owl .hand {
  width: 34px;
  height: 34px;
  border-radius: 40px;
  background-color: #472d20;
  -webkit-transform: scaleY(0.6);
          transform: scaleY(0.6);
  position: absolute;
  left: 14px;
  bottom: -8px;
  transition: 0.3s ease-out;
}
.owl .hand.password {
  -webkit-transform: translateX(42px) translateY(-15px) scale(0.7);
          transform: translateX(42px) translateY(-15px) scale(0.7);
}
.owl .hand.hand-r {
  left: 170px;
}
.owl .hand.hand-r.password {
  -webkit-transform: translateX(-42px) translateY(-15px) scale(0.7);
          transform: translateX(-42px) translateY(-15px) scale(0.7);
}
.owl .arms {
  position: absolute;
  top: 58px;
  height: 41px;
  width: 100%;
  overflow: hidden;
}
.owl .arms .arm {
  width: 40px;
  height: 65px;
  background-image: url("https://dash.readme.io/img/owl-login-arm.png");
  position: absolute;
  left: 20px;
  top: 40px;
  transition: 0.3s ease-out;
}
.owl .arms .arm.password {
  -webkit-transform: translateX(40px) translateY(-40px);
          transform: translateX(40px) translateY(-40px);
}
.owl .arms .arm.arm-r {
  left: 158px;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}
.owl .arms .arm.arm-r.password {
  -webkit-transform: translateX(-40px) translateY(-40px) scaleX(-1);
          transform: translateX(-40px) translateY(-40px) scaleX(-1);
}
.form {
  margin: auto;
  margin-top: -9px;
  padding: 30px;
  background-color: #fff;
  background-image: url("https://cdn141.picsart.com/244492810024202.jpg");
  background-size: 90%;
  width: 300px;
  height: 400px;
  border-radius: 20px;
}

.form .control {
  margin-bottom: 15px;
  position: relative;
}
.form .control label {
  position: absolute;
  font-size: 16px;
  top: 11px;
  left: 9px;
  color: rgba(0,0,0,0.3);
}
.form .control input {
  padding: 9px 6px 9px 30px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 14px;
}

#login-button
{
  width: 100%;
  margin: auto;
}

#signup-text
{
  text-align: center;
}

.ant-input[disabled] {
    color: rgba(0, 0, 0, 0.7) !important;
    background-color: rgb(243, 243, 243);
    cursor: not-allowed;
    opacity: 1;
}
.events {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .events .ant-badge-status {
    overflow: hidden;
    white-space: nowrap;
    width: 100%;
    text-overflow: ellipsis;
    font-size: 12px;
  }
  .notes-month {
    text-align: center;
    font-size: 28px;
  }
  .notes-month section {
    font-size: 28px;
  }

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

#login-body {
    position: fixed;
    font-family: 'Raleway', sans-serif;
    background: #344a72 !important;
    color: #fff;
    line-height: 1.8;
    height: 100vh;
    width: 100vw;
}

a {
    text-decoration: none;
}

#login-container {
    margin: 60px auto;
    max-width: 400px;
    padding: 20px;
  
}

.login-form-wrap {
    background: #fff;
    padding: 70px 25px;
    color: #333;
      /* border-radius: 10px; */
}

.login-form-wrap h1,
.login-form-wrap p {
    text-align: center;
}

.login-form-wrap .login-form-group {
    margin-top: 15px;
}

.login-form-wrap .login-form-group label {
    display: block;
    color: #666;
}

.login-form-wrap .login-form-group input {
    width: 100%;
    padding: 10px;
    border: #ddd 1px solid;
    border-radius: 5px;
}

.login-form-wrap button {
    display: block;
    width: 100%;
    padding: 10px;
    margin-top: 20px;
    background: #43aa8b;
    color: #fff;
    cursor: pointer;
    border: none;
    border-radius: 5px;
}

.login-form-wrap button:hover {
    background: #37a08e
}

.login-form-wrap .login-bottom-text {
    font-size: 13px;
    margin-top: 20px;
   
}

.login-form-wrap .login-bottom-text a {

    color: black;
}


footer {
    text-align: center;
    margin-top: 10px;
}

footer a {
    color: #49c1a2
}
.ant-menu-item {
    margin : 0 !important
}

.ant-menu::-webkit-scrollbar{
    display: none;
}

.ant-menu-sub{
    max-height: 450px;
    overflow-y: scroll;
}
#topbar-avatar{
    position: absolute;
    right: 15px;
    cursor: pointer;
    height: 50px;
}

/* ID */
 
*{
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

.pcview{
    display: block;
}   

.mbview{
    display: none;
}

/* Customize website's scrollbar like Mac OS
Not supports in Firefox and IE */

/* total width */
::-webkit-scrollbar {
    background-color:#fff;
    width:10px
}

/* background of the scrollbar except button or resizer */
::-webkit-scrollbar-track {
    background-color:#fff
}

/* scrollbar itself */
::-webkit-scrollbar-thumb {
    background-color:#babac0;
    border-radius:10px;
    border:1px solid #fff
}

/* set button(top and bottom of the scrollbar) */
::-webkit-scrollbar-button {display:none}


@media all and (max-width: 600px) {
    
    .pcview{
        display: none !important;
    }

    .mbview{
        display: block !important;
    }
    
    
}

